.home-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, #E75481 -22.84%, #929CDE 113.36%);
}

.home-container .logo {
  position: absolute;
  top: 1.33rem;
  left: 0.43rem;
  width: 1.6rem;
  height: 1.6rem;
}

.home-container .overview-panel {
  position: absolute;
  width: 6.13rem;
  height: 6.59rem;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: #FBFBFB;
  box-shadow: 0.11rem 0 0.53rem rgba(0, 0, 0, 0.25);
  border-radius: 0.8rem;
}

.home-container .overview-panel .location {
  display: inline-block;
  margin-top: 2.13rem;
  font-family: 'Alegreya Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 0.53rem;
  line-height: 0.64rem;
  color: #332821;
}

.home-container .overview-panel .location+div {
  display: flex;
  justify-content: space-between;
  padding-left: 0.53rem;
  text-align: left;
}

.home-container .overview-panel .location+div .temp {
  position: relative;
  font-family: 'Alegreya Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 1.71rem;
  line-height: 2.05rem;
  color: #332821;
}

.home-container .overview-panel .location+div .temp::after {
  content: 'Â°C';
  position: absolute;
  top: 0.53rem;
  right: -0.32rem;
  font-family: 'Alegreya Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 0.37rem;
  line-height: 0.45rem;
  color: #332821;
}

.home-container .overview-panel .location+div .time {
  position: relative;
  top: -0.21rem;
  font-family: 'Alegreya Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 0.37rem;
  line-height: 0.45rem;
  color: rgba(51, 40, 33, 0.5);
}

.home-container footer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
  left: 50%;
  bottom: 0.53rem;
  transform: translateX(-50%);
  width: 9.15rem;
  height: 1.71rem;
  padding: 0 0.96rem;
  background-color: #fbfbfb;
  border-radius: 0.53rem;
}

.home-container footer img {
  width: 0.8rem;
  height: 1.01rem;
}